/** @format */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-font: "Kumbh Sans", sans-serif;
  --grayish-blue: hsl(220, 14%, 75%);
  --black: hsl(0, 0%, 0%);
  --very-dark-blue: hsl(220, 13%, 13%);
}

.navContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
}

.navContainer svg {
  color: var(--grayish-blue);
}

.navContainer .hamburger {
  font-family: var(--primary-font);
  font-weight: 700;
}

.navContainer .navItemsContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 12px 16px;
  transition: all 0.3s ease;
}

.navContainer .navItemsContainer .hamburger {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navContainer .navItemsContainer .navPC {
  display: none;
}

.navContainer .navItemsContainer .hamburger h1 {
  margin: 0 12px;
  font-family: "Kumbh Sans", sans-serif;
}

.navContainer .navItemsContainer .cart {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navContainer .navItemsContainer .cart img {
  width: 24px;
  height: 24px;
}

.navContainer .navItemsContainer .cart svg.cartIcon {
  margin: 0 12px;
}

.navContainer .dropdown {
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease;
}

.navContainer .sidebarItems {
  position: absolute;
  top: 18px;
  right: 0;
  left: 16px;
  transition: all 0.3s ease;
}

.navContainer .sidebarItems > svg {
  margin-bottom: 30px;
}
.navContainer .dropdown .sidebar .sidebarLinks {
  display: flex;
  flex-direction: column;

  font-family: var(--primary-font);
}

.navContainer .dropdown .sidebar .sidebarLinks a {
  text-decoration: none;
  margin: 10px 0;
  font-family: var(--primary-font);
  font-weight: 700;
  color: var(--very-dark-blue);
}

.navContainer .dropdown .sidebarBackground {
  width: 60%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: white;
  transition: all 0.3s ease;
}

@media (min-width: 1024px) {
  .navContainer .navItemsContainer {
    margin: 18px 100px;
  }
  .navContainer .navItemsContainer svg.hamburger {
    display: none;
  }

  .navContainer .navItemsContainer .cart img {
    height: 40px;
    width: 40px;
  }

  .navContainer .navItemsContainer .cart svg.cartIcon {
    margin: 0 30px;
  }

  .navContainer .navItemsContainer .hamburger h1 {
    font-size: 32px;
    margin: 0;
  }

  .navContainer .navItemsContainer .navPC {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .navContainer .navItemsContainer .navPC a {
    font-family: var(--primary-font);
    text-decoration: none;
    margin: 0 5px;
    font-size: 16px;
    color: var(--grayish-blue);
  }

  .navContainer .navItemsContainer {
    padding-bottom: 50px;
    border-bottom: 1px var(--grayish-blue) solid;
  }
}
